@import url("https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300;400;500&family=Open+Sans:wght@400;600&display=swap");
* {
  box-sizing: inherit;
}

*::before, *::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  line-height: 1;
  text-rendering: optimizeSpeed;
  text-decoration-skip: objects;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  background-color: transparent;
  margin: 0;
}

p,
dd,
dl,
figure {
  margin: 0;
}

blockquote {
  margin: 0;
  quotes: none;
}

q {
  quotes: none;
}

ul.ul,
ol.ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

th {
  font-weight: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
  margin: 0;
}

audio,
video {
  display: block;
}

img {
  border: none;
}

img,
svg {
  vertical-align: middle;
}

iframe {
  border: none;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit;
}

a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
}

abbr {
  border: none;
  text-decoration: none;
}

b,
strong {
  font-weight: inherit;
}

i,
em,
dfn {
  font-style: inherit;
}

mark {
  background-color: transparent;
  color: inherit;
}

small {
  font-size: inherit;
}

sub,
sup {
  position: relative;
  font-size: inherit;
  line-height: 0;
}

sub {
  bottom: -.25em;
}

button,
input,
optgroup,
select,
textarea {
  font: inherit;
  color: inherit;
  letter-spacing: inherit;
  border: none;
  border-radius: 0;
  box-shadow: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
}

button:focus,
input:focus,
select:focus,
textarea:focus {
  outline: none;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-align: left;
  text-transform: none;
}

input.input,
select.select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input.input::-ms-expand,
select.select::-ms-expand {
  display: none;
}

button,
[type='button'],
[type='reset'],
[type='submit'] {
  cursor: pointer;
  -webkit-appearance: none;
}

textarea {
  resize: none;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

legend {
  display: block;
  padding: 0;
  white-space: normal;
}

:focus {
  outline: none;
}

::placeholder {
  font-size: inherit;
  color: inherit;
  opacity: inherit;
}

button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  border: none;
  padding: 0;
}

button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
  outline: none;
}

[type='number']::-webkit-inner-spin-button, [type='number']::-webkit-outer-spin-button {
  height: auto;
  margin: 0;
  -webkit-appearance: none;
  -moz-appearance: textfield;
}

[type='search'] {
  outline: none;
}

[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

[hidden],
.hidden {
  display: none;
}

:disabled,
[disabled],
.disabled {
  cursor: not-allowed;
}

::-ms-clear {
  display: none;
}

:-webkit-autofill {
  box-shadow: 0 0 1000px #fff inset;
  -webkit-text-fill-color: currentColor;
}

@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after
html {
    animation: none !important;
    transition-duration: 0s !important;
  }
}

:root {
  --XS: 375;
  --S: 576;
  --M: 768;
  --L: 992;
  --XL: 1200;
  --XXL: 1600;
}

:root {
  --font-main: 'Open Sans', sans-serif;
  --font-second: 'Fira Sans', sans-serif;
  --color-black_1: #3D3D3D;
  --color-black_2: #6A6A6A;
  --color-black_3: #A8A8A8;
  --color-blue_2: #0071CE;
  --color-blue_2_hover: #0063b5;
  --color-blue_5: #1C98FF;
  --color-note: #FF3039;
  --color-accent_2: #17BA5F;
  --color-accent_2_hover: #14a353;
  --color-accent_3: #FFD200;
  --nav-height: 7rem;
  --transition: 300ms;
  --container: 141rem;
  --padding: 1.5rem;
}

@media (min-width: 768px) {
  :root {
    --padding: 3rem;
  }
}

@media (min-width: 992px) {
  :root {
    --padding: 6rem;
  }
}

body {
  background-color: #fff;
  color: var(--color-black_2);
  font-family: var(--font-main);
  font-size: 1.6rem;
  font-weight: 400;
}

strong {
  font-weight: 700;
}

p {
  line-height: 1.75;
}

::selection {
  color: #fff;
  background-color: #000;
}

.title {
  font-family: var(--font-second);
  font-size: 3.2rem;
  font-weight: 300;
  line-height: 1.38;
}

@media (min-width: 768px) {
  .title {
    font-size: 3.6rem;
  }
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.container {
  width: 100%;
  max-width: calc(var(--container) + var(--padding) * 2);
  margin: 0 auto;
  padding-left: var(--padding);
  padding-right: var(--padding);
}

.underline {
  text-decoration: underline;
}

.text-center {
  text-align: center;
}

.badge {
  display: inline-block;
  padding-left: .7rem;
  padding-right: .7rem;
  color: #fff;
  text-align: center;
  background-color: var(--color-note);
  border-radius: .3rem;
  font-family: var(--font-second);
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 2.4rem;
  text-transform: uppercase;
  white-space: nowrap;
}

.block {
  width: 100%;
  min-height: 7.6rem;
  display: flex;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: #fff;
  box-shadow: 0 0.2rem 0.3rem rgba(0, 0, 0, 0.15);
}

.menu-open .block .container {
  padding-right: calc(var(--padding) + var(--scrollPadding, 0rem));
}

.block.is-sticky {
  z-index: 5;
  position: fixed;
  top: var(--nav-height);
}

.block__wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 1.5rem;
}

.block__link {
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 1.5rem;
  align-items: center;
  justify-content: flex-start;
  color: #3C3C3C;
  font-family: var(--font-second);
  font-weight: 500;
  font-size: 2.2rem;
}

.block .btn {
  background-color: var(--color-accent_2);
  font-size: 1.3rem;
  letter-spacing: .15em;
}

.block .btn:hover {
  background-color: var(--color-accent_2_hover);
}

.box {
  position: relative;
}

.box__bnt {
  min-width: 2.4rem;
  min-height: 2.4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  user-select: none;
  cursor: pointer;
}

.box__back {
  position: absolute;
  top: 3rem;
  left: 3rem;
}

@media (min-width: 992px) {
  .box__back {
    display: none;
  }
}

.box__content {
  z-index: 1;
  position: fixed;
  bottom: 0;
  right: 0;
  transform: translateX(100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-color: #283440;
  transition: transform .3s, opacity .3s;
}

@media (min-width: 992px) {
  .box__content {
    position: absolute;
    transform: translateY(110%);
    width: initial;
    height: initial;
    min-width: 30rem;
    min-height: 30rem;
    background-color: #353740;
    box-shadow: rgba(100, 100, 111, 0.2) 0 0.7rem 2.7rem 0;
    pointer-events: none;
    opacity: 0;
  }
}

.box.is-active .box__content {
  transform: translateX(0);
}

@media (min-width: 992px) {
  .box.is-active .box__content {
    transform: translateY(100%);
    pointer-events: auto;
    opacity: 1;
  }
}

.basket__count {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(40%, -30%);
  min-width: 1.6rem;
  min-height: 1.6rem;
  line-height: 1.6rem;
  color: #fff;
  border-radius: 50%;
  background-color: var(--color-blue_5);
  font-family: var(--font-second);
  font-weight: 500;
  font-size: 1.2rem;
  text-align: center;
}

.search input[type='Search'] {
  padding: .5rem 1.5rem;
  line-height: 4rem;
  border: 0.1rem solid rgba(255, 255, 255, 0.3);
}

.btn {
  display: inline-block;
  padding-left: 2.4rem;
  padding-right: 2.4rem;
  color: #fff;
  background-color: var(--color-blue_2);
  font-family: var(--font-second);
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 4.8rem;
  letter-spacing: .08em;
  text-transform: uppercase;
  white-space: nowrap;
  transition: background-color .3s;
}

.btn:hover {
  background-color: var(--color-blue_2_hover);
}

.btn--white {
  color: var(--color-black_1);
  background-color: #fff;
}

.btn--white:hover {
  background-color: #f2f2f2;
}

.btn--icon {
  min-height: 4.8rem;
  line-height: 1;
  display: inline-flex;
  align-items: center;
  gap: 1rem;
}

.btn--border {
  color: var(--color-blue_2);
  border: .1rem solid #ddd;
}

.play-btn {
  z-index: 3;
  position: relative;
  width: 4.8rem;
  height: 4.8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #fff;
  transition: opacity .3s;
}

.play-btn::after {
  z-index: 1;
  content: '';
  position: relative;
  right: -.2rem;
  width: 0;
  height: 0;
  border-top: 1rem solid transparent;
  border-left: 1.6rem solid #27282C;
  border-bottom: 1rem solid transparent;
}

.accordion {
  display: grid;
}

.accordion__item {
  border-top: .1rem solid #F2F3F4;
}

.accordion__item:last-child {
  border-bottom: .1rem solid #F2F3F4;
}

.accordion__btn {
  position: relative;
  width: 100%;
  min-height: 7rem;
  padding-left: 1.6rem;
  padding-right: 3.6rem;
  user-select: none;
}

.accordion__btn::after {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(-45deg);
  right: 1rem;
  width: 1rem;
  height: 1rem;
  border: 0.2rem solid var(--color-black_1, #3D3D3D);
  border-top: 0;
  border-right: 0;
}

.accordion__btn.is-active::after {
  transform: translateY(-50%) rotate(135deg);
}

.accordion__title {
  position: relative;
  display: flex;
  align-items: center;
  gap: 1rem;
  color: var(--color-black_1);
  font-family: var(--font-second);
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 1;
}

.accordion__panel {
  max-height: 0;
  background-color: #F9F9F9;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}

.accordion__panel-box {
  padding: 1.8rem 2.6rem 1.5rem 2.4rem;
}

.accordion__wrap {
  display: flex;
  align-items: center;
  gap: 1.6rem;
}

.tabs {
  margin-left: calc(-1 * var(--padding));
  margin-right: calc(-1 * var(--padding));
}

@media (min-width: 768px) {
  .tabs {
    margin-left: 0;
    margin-right: 0;
  }
}

.tabs__content {
  display: none;
  padding-left: var(--padding);
  padding-right: var(--padding);
  opacity: 0;
}

@media (min-width: 768px) {
  .tabs__content {
    padding-left: 0;
    padding-right: 0;
  }
}

.tabs__content.is-active {
  display: block;
  animation: show .3s forwards;
}

.tabs__content img {
  max-width: 100%;
}

.tabs__content-wrap {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  gap: 3rem;
}

@media (min-width: 1200px) {
  .tabs__content-wrap {
    flex-direction: row;
  }
}

.tabs__content-title {
  margin-bottom: 1.7rem;
  color: var(--color-black_1);
  font-family: var(--font-second);
  font-size: 2.6rem;
  font-weight: 400;
  line-height: 1.24;
}

.tabs__content-video {
  position: relative;
  width: 100%;
  height: 20rem;
  max-width: 57rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  margin: auto;
}

@media (min-width: 768px) {
  .tabs__content-video {
    height: 34.7rem;
  }
}

.tabs__content-video::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(290.16deg, #1E1229 -1.68%, #50147D 104.07%);
  transition: background-image .3s;
}

.tabs__content-video.hide-bg::after {
  background-image: none;
}

.tabs__content-video.is-play video {
  z-index: 2;
}

.tabs__content-video.is-play .play-btn {
  opacity: 0;
  pointer-events: none;
}

.tabs__content-video video {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.tabs__content-img {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity .3s;
}

.tabs__content-img.is-hide {
  opacity: 0;
  pointer-events: none;
}

.tabs__content-desc {
  max-width: 75rem;
}

.tabs__content-desc p:not(:first-of-type) {
  margin-top: 1.5rem;
}

.tabs__items {
  position: relative;
  width: 100%;
  flex-shrink: 0;
  overflow-y: hidden;
  overflow-x: auto;
}

@media (min-width: 768px) {
  .tabs__items {
    width: initial;
  }
  .tabs__items::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: .2rem;
    background-color: #E8E9EB;
  }
}

.tabs__item {
  position: relative;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  color: var(--color-black_3);
  font-family: var(--font-second);
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 5rem;
  white-space: nowrap;
  user-select: none;
  transition: color .3s;
}

.tabs__item:hover {
  color: var(--color-black_1);
}

.tabs__item:hover path {
  fill: var(--color-black_1);
}

.tabs__item::before, .tabs__item::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: .2rem;
}

.tabs__item::before {
  background-color: #E8E9EB;
}

@media (min-width: 768px) {
  .tabs__item::before {
    display: none;
  }
}

.tabs__item::after {
  display: none;
  background-color: var(--color-blue_2);
}

.tabs__item.is-active {
  pointer-events: none;
  color: var(--color-black_1);
}

.tabs__item.is-active::after {
  display: block;
}

.tabs__item.is-active path {
  fill: var(--color-black_1);
}

.tabs__item path {
  transition: fill .3s;
}

.tabs--vertical {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 3rem;
}

@media (min-width: 768px) {
  .tabs--vertical {
    flex-direction: row;
    gap: 5rem;
  }
}

.tabs--vertical .tabs__items {
  display: inline-flex;
  flex-direction: row;
}

@media (min-width: 768px) {
  .tabs--vertical .tabs__items {
    flex-direction: column;
  }
  .tabs--vertical .tabs__items::before {
    width: .2rem;
    height: 100%;
  }
}

@media (min-width: 768px) {
  .tabs--vertical .tabs__item {
    padding-left: 2.8rem;
  }
}

@media (min-width: 768px) {
  .tabs--vertical .tabs__item::after {
    bottom: initial;
    top: 0;
    width: .2rem;
    height: 100%;
  }
}

.tabs--vertical .tabs__content img {
  filter: drop-shadow(0.3rem 0.5rem 1.6rem rgba(0, 0, 0, 0.1));
}

@media (min-width: 1200px) {
  .tabs--vertical .tabs__content img {
    margin-top: -5.7rem;
  }
}

.tabs--horizontal .tabs__items {
  display: flex;
}

@media (min-width: 768px) {
  .tabs--horizontal .tabs__items {
    gap: 2rem;
  }
  .tabs--horizontal .tabs__items::before {
    height: .3rem;
  }
}

@media (min-width: 992px) {
  .tabs--horizontal .tabs__items {
    gap: 4rem;
  }
}

.tabs--horizontal .tabs__item {
  display: inline-flex;
  gap: 1rem;
  font-weight: 500;
  font-size: 1.6rem;
  letter-spacing: .08em;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .tabs--horizontal .tabs__item {
    padding-left: 0;
    padding-right: 0;
    line-height: 7.2rem;
  }
}

@media (min-width: 768px) {
  .tabs--horizontal .tabs__item::after {
    height: .3rem;
  }
}

.tabs--horizontal .tabs__contents {
  padding-top: 3rem;
}

@media (min-width: 768px) {
  .tabs--horizontal .tabs__contents {
    padding-top: 5rem;
  }
}

.slider {
  margin-left: calc(-1 * var(--padding)) !important;
  margin-right: calc(-1 * var(--padding)) !important;
  user-select: none;
}

@media (min-width: 768px) {
  .slider {
    margin-left: auto !important;
    margin-right: auto !important;
    margin-top: -4.4rem;
  }
}

.slider__item {
  color: var(--color-black_1);
}

.slider__item-bg {
  position: relative;
  height: 21rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2.6rem;
}

.slider__item-bg::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(61, 61, 61, 0.4);
}

.slider__item-content {
  padding-left: var(--padding);
  padding-right: var(--padding);
}

@media (min-width: 768px) {
  .slider__item-content {
    padding-left: 0;
    padding-right: 0;
  }
}

.slider__item-img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider__item-logo {
  z-index: 1;
  position: relative;
  max-width: 100%;
}

.slider__item-title {
  margin-bottom: .2rem;
  font-family: var(--font-second);
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 1.22;
}

.slider__item-desc {
  margin-bottom: 1.7rem;
}

.slider__item-link {
  min-height: 3.6rem;
  display: inline-flex;
  align-items: center;
  padding: .1rem 1.2rem .1rem .4rem;
  background-color: #F9FAFC;
  border: .1rem solid #F2F3F4;
  font-weight: 600;
  font-size: 1.4rem;
  transition: background-color .3s;
}

.slider__item-link:hover {
  background-color: #F2F3F4;
}

.swiper {
  padding-top: 2rem !important;
  padding-bottom: 5rem !important;
}

@media (min-width: 768px) {
  .swiper {
    padding-top: 9rem !important;
    padding-bottom: 0 !important;
  }
}

.swiper-nav {
  z-index: 1;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  gap: 2rem;
}

@media (min-width: 768px) {
  .swiper-nav {
    bottom: initial;
    top: 0;
    left: initial;
    right: 0;
