  padding-right: var(--scrollPadding);
}

body.menu-open::after {
  z-index: 7;
  pointer-events: auto;
  transition: background-color var(--transition);
  background-color: rgba(0, 0, 0, 0.5);
}

body.close-transition::after {
  transition: background-color var(--transition);
}

